const systemInfo = uni.getSystemInfoSync();
export default {
	data () {
		return {
			animation: false,
			platform: systemInfo.platform
		}
	},
	props: {
		/*
		* 参数说明（定位）
		*/
		position: {//可能值  top  left  right bottom middle
			type: String,
			default: 'middle'
		},
		/*
		* 参数说明
		* full 宽度100%
		* insert 80%宽度内联小框
		* cover 宽度高度100%
		*/
		mode: {
			type: String,
			default: 'full'
		},
		mask: {
			type: [Boolean, String],
			default: true
		},
		/*
		* H5置顶
		*/
		H5Top: {
			type: [Boolean, String],
			default: true
		},
		/*
		* H5置底
		*/
		H5Bottom: {
			type: [Boolean, String],
			default: true
		},
	},
	computed: {
		_mask() {
			return String(this.mask) === 'false' ? false : true;
		},
		_H5Top() {
			return String(this.H5Top) === 'false' ? false : true;
		},
		_H5Bottom() {
			return String(this.H5Bottom) === 'false' ? false : true;
		}
	},
	watch: {
		animation(val) {
			this.$emit('change', val);
		}
	},
	methods: {
		moveHandle() {
			return ;
		},
		show () {
			this.animation = true;
			return true;
		},
		hide () {
			this.animation = false;
			return false;
		},
		toggle () {
			return !this.animation ? this.show() : this.hide()
		}
	}
}
